@granola: "../lib";
//@import "@{granola}/granola";
//@import "@{granola}/elements/default";
//@import "@{granola}/ui/base";

@plugin "../lib/granola";
@set {
    options {
        ui: default;
        elements: default;
        theme: default;
    }
    colors {
    	dark {
    	    color: black;
    	}
    	light {
    	    color: white;
    	}
    }
}
.block when (get('options.theme') = default) {
    @col: ~"colors.light";
    color: get('@{col}.color');
}

.test {
    @define colors {
        color: #888;
    }
    color: get('colors.color');
}

